"use client";

import React, { useEffect, useRef, useState } from "react";
import { Chart, ArcElement, Tooltip as ChartTooltip, Legend } from "chart.js";
import { Doughnut } from "react-chartjs-2";
import ChartDataLabels from "chartjs-plugin-datalabels";
import Image from "next/image";
import { API_URL } from "../api/apiURL";

Chart.register(ArcElement, ChartTooltip, Legend, ChartDataLabels);

/**
 * Custom plugin to paint a 3D-like extrusion and shadow on Doughnut segments.
 * It draws a subtle gradient ring below the main doughnut to simulate depth.
 */
const doughnut3DPlugin = {
  id: "doughnut3d",
  beforeDraw(chart) {
    const { ctx, chartArea, data } = chart;
    if (!chartArea) return;
    const centerX = (chartArea.left + chartArea.right) / 2;
    const centerY = (chartArea.top + chartArea.bottom) / 2 + 6; // shift slightly down for perspective
    const outerRadius = chart._metasets[0].data[0].outerRadius;
    const innerRadius = chart._metasets[0].data[0].innerRadius;

    // draw a faint ellipical shadow/depth under the doughnut
    ctx.save();
    const gradient = ctx.createRadialGradient(centerX, centerY + 8, innerRadius * 0.2, centerX, centerY + 20, outerRadius * 1.2);
    gradient.addColorStop(0, "rgba(0,0,0,0.06)");
    gradient.addColorStop(1, "rgba(0,0,0,0.00)");
    ctx.fillStyle = gradient;
    ctx.beginPath();
    ctx.ellipse(centerX, centerY + 10, outerRadius * 1.05, outerRadius * 0.48, 0, 0, Math.PI * 2);
    ctx.fill();
    ctx.restore();
  },
};

Chart.register(doughnut3DPlugin);

function formatNumber(n) {
  return n.toLocaleString();
}

export default function AnalyticsPageChartJS() {
  const [loading, setLoading] = useState(true);
  const [signInData, setSignInData] = useState([]);
  const [genderData, setGenderData] = useState([
    { name: "Male", value: 60 },
    { name: "Female", value: 30 },
    { name: "Others", value: 10 },
  ]);
  const [matchData, setMatchData] = useState([
    { name: "Still Looking", value: 30 },
    { name: "Successfully Matched", value: 45 },
    { name: "Newly Registered", value: 20 },
    { name: "Inactive", value: 5 },
  ]);
  const [totalJuly, setTotalJuly] = useState(23000000);
  const [totalJune, setTotalJune] = useState(17000000);

  useEffect(() => {
    // Example fetch - replace with your /admin/getByGender if available
    const fetchData = async () => {
      try {
        // const res = await fetch(`${API_URL}/admin/getByGender`);
        // const data = await res.json();
        // process and set real data...
        // For demo we use static values similar to screenshot
        const demoSignIns = Array.from({ length: 30 }).map((_, i) => ({
          day: (i + 1).toString().padStart(2, "0"),
          july: Math.round(11000000 + (i * 300000) + (Math.sin(i) * 400000)),
          june: Math.round(5000000 + (Math.cos(i) * 500000)),
        }));
        setSignInData(demoSignIns);
        setLoading(false);
      } catch (e) {
        console.error(e);
        setLoading(false);
      }
    };
    fetchData();
  }, []);

  if (loading)
    return (
      <div className="p-8 flex justify-center">
        <Image src="/loading2.gif" width={80} height={80} alt="loading" />
      </div>
    );

  // ---------- Chart.js config for the donut (3D look)
  const genderColors = ["#3ad85b", "#f6d24b", "#f06a5b"]; // green, yellow, red-ish
  const genderDataset = {
    labels: genderData.map((g) => g.name),
    datasets: [
      {
        data: genderData.map((g) => g.value),
        backgroundColor: genderColors,
        borderColor: "#fff",
        borderWidth: 2,
        hoverOffset: 12, // makes slice "pop" out
      },
    ],
  };

  const matchColors = ["#22c55e", "#f97316", "#3b82f6", "#9ca3af"];
  const matchDataset = {
    labels: matchData.map((m) => m.name),
    datasets: [
      {
        data: matchData.map((m) => m.value),
        backgroundColor: matchColors,
        borderColor: "#fff",
        borderWidth: 2,
        hoverOffset: 14,
      },
    ],
  };

  const smallFont = { size: 12, color: "#333" };

  // sign-in line chart is done later with native HTML/SVG? here we use a simple SVG line (lightweight).
  // But for consistency, we present sign-in as an accessible SVG line plot that mimics screenshot style.

  return (
    <div className="p-5 md:p-8 bg-gray-50 min-h-screen flex flex-wrap gap-6">
      {/* LEFT LARGE CARD */}
      <div className="w-full lg:w-[60%] bg-white rounded-xl shadow-lg border border-gray-200 p-6">
        <div className="flex justify-between items-start mb-4">
          <div>
            <h2 className="text-xl font-semibold">Sign-In Analytics</h2>
            <p className="text-4xl md:text-5xl font-extrabold mt-2">{formatNumber(totalJuly)}</p>
            <div className="mt-1 text-sm text-green-600 inline-flex items-center gap-2 font-semibold">
              <span className="bg-green-50 px-2 py-1 rounded-md border border-green-200 text-green-700">↑ 12%</span>
              <span className="text-gray-600 font-normal">Vs last Month</span>
            </div>
          </div>

          <div className="flex items-center gap-4">
            <div className="flex items-center gap-2">
              <span className="w-3 h-3 bg-black rounded-full"></span> <span>July</span>
            </div>
            <div className="flex items-center gap-2 text-gray-500">
              <span className="w-3 h-3 bg-gray-300 rounded-full"></span> <span>June</span>
            </div>
            <div>
              <select className="border rounded-md px-2 py-1 text-sm">
                <option>Month</option>
              </select>
            </div>
          </div>
        </div>

        {/* SVG line chart that visually matches screenshot (grid + two lines + tooltips) */}
        <div className="w-full h-[380px] bg-white">
          <div className="w-full h-full p-3">
            <svg viewBox="0 0 1200 360" preserveAspectRatio="none" className="w-full h-full">
              {/* horizontal grid lines */}
              {[0, 1, 2, 3, 4, 5].map((i) => (
                <line key={i} x1="60" x2="1160" y1={20 + i * 60} y2={20 + i * 60} stroke="#e6e6e6" strokeWidth="1" />
              ))}

              {/* axis labels left */}
              {[0, 1, 2, 3, 4, 5].map((i) => (
                <text key={i} x="20" y={24 + i * 60} style={{ fontSize: 12, fill: "#888" }}>
                  {`${(45 - i * 7)} M`}
                </text>
              ))}

              {/* June dashed line */}
              <path
                d={(() => {
                  const points = signInData.map((d, i) => {
                    const x = 60 + (i / (signInData.length - 1)) * 1100;
                    const normalized = (d.june / 45000000) * 320;
                    const y = 340 - normalized;
                    return `${x},${y}`;
                  });
                  return "M" + points.join(" L ");
                })()}
                fill="none"
                stroke="#9b9b9b"
                strokeWidth="2"
                strokeDasharray="6 6"
              />

              {/* July solid line */}
              <path
                d={(() => {
                  const points = signInData.map((d, i) => {
                    const x = 60 + (i / (signInData.length - 1)) * 1100;
                    const normalized = (d.july / 45000000) * 320;
                    const y = 340 - normalized;
                    return `${x},${y}`;
                  });
                  return "M" + points.join(" L ");
                })()}
                fill="none"
                stroke="#000"
                strokeWidth="3"
                strokeLinejoin="round"
                strokeLinecap="round"
              />

              {/* last point highlight & tooltip bubble imitation */}
              {(() => {
                const last = signInData[signInData.length - 1];
                const i = signInData.length - 1;
                const x = 60 + (i / (signInData.length - 1)) * 1100;
                const normalized = (last.july / 45000000) * 320;
                const y = 340 - normalized;
                return (
                  <>
                    <circle cx={x} cy={y} r="7" fill="#000" stroke="#fff" strokeWidth="3" />
                    {/* black tooltip rectangle */}
                    <rect x={x - 120} y={y - 80} rx="8" ry="8" width="180" height="46" fill="#111" />
                    <text x={x - 110} y={y - 52} fill="#fff" style={{ fontSize: 12, fontWeight: 700 }}>
                      July 07, 2025
                    </text>
                    <text x={x - 110} y={y - 34} fill="#fff" style={{ fontSize: 12 }}>
                      {formatNumber(last.july)}
                    </text>
                  </>
                );
              })()}
            </svg>
          </div>
        </div>
      </div>

      {/* RIGHT COLUMN */}
      <div className="w-full lg:w-[36%] flex flex-col gap-6">
        {/* Gender */}
        <div className="bg-white rounded-xl shadow-lg border p-5">
          <div className="flex justify-between items-center mb-3">
            <h3 className="font-semibold text-lg">Gender Analytics</h3>
            <div className="flex gap-3 text-xs">
              <span className="flex items-center gap-2"><span className="w-3 h-3 bg-green-400 rounded-full border"></span>Male</span>
              <span className="flex items-center gap-2"><span className="w-3 h-3 bg-yellow-400 rounded-full border"></span>Female</span>
              <span className="flex items-center gap-2"><span className="w-3 h-3 bg-red-400 rounded-full border"></span>Ots.</span>
            </div>
          </div>
          <div className="w-full h-[220px]">
            <Doughnut
              data={genderDataset}
              options={{
                plugins: {
                  legend: { display: false },
                  tooltip: {
                    backgroundColor: "#111",
                    titleColor: "#fff",
                    bodyColor: "#fff",
                    callbacks: {
                      label: (ctx) => `${ctx.label}: ${ctx.parsed} %`,
                    },
                  },
                  datalabels: {
                    color: "#222",
                    formatter: (value, ctx) => {
                      const pct = Math.round((value / ctx.dataset.data.reduce((a, b) => a + b, 0)) * 100);
                      return `${ctx.chart.data.labels[ctx.dataIndex]} ${pct}%`;
                    },
                    anchor: "center",
                    clamp: true,
                    font: { weight: "700", size: 12 },
                  },
                },
                cutout: "40%",
                responsive: true,
                maintainAspectRatio: false,
              }}
            />
          </div>
        </div>

        {/* Matchmaking */}
        <div className="bg-white rounded-xl shadow-lg border p-5">
          <div className="flex justify-between items-center mb-3">
            <h3 className="font-semibold text-lg">Matchmaking Status</h3>
            <div className="flex flex-col text-xs text-right">
              <span className="flex gap-2 items-center"><span className="w-3 h-3 bg-green-600 rounded-full"></span>Still Looking</span>
            </div>
          </div>

          <div className="w-full h-[220px]">
            <Doughnut
              data={matchDataset}
              options={{
                plugins: {
                  legend: { display: false },
                  tooltip: { backgroundColor: "#111", titleColor: "#fff", bodyColor: "#fff" },
                  datalabels: {
                    color: "#222",
                    formatter: (value, ctx) => `${ctx.chart.data.labels[ctx.dataIndex]} ${(Math.round(value))}%`,
                    font: { weight: "700", size: 12 },
                  },
                },
                cutout: "50%",
                responsive: true,
                maintainAspectRatio: false,
              }}
            />
          </div>

          <p className="text-center text-sm italic text-gray-600 mt-3">“Out Of 20M, 5M Is Matched”</p>
        </div>
      </div>
    </div>
  );
}
